*,
html {
  margin: 0;
  padding: 0;
}

.active {
  color: #44aeeb;
  transition: 1s;
}
.contact-active {
  color: #44aeeb;
  transition: 1s;
}

.navbar {
  position: fixed;
  width: 100%;
  z-index: 99;
  padding-top: 40px;
  transition: 1s;
}
.menu-wrap {
  position: fixed;
  top: 0;
  left: 0;
  z-index: 98;
}
.menu-wrap .toggler {
  position: absolute;
  top: 0;
  left: 0;
  float: right;
  z-index: 99;
  cursor: pointer;
  width: 40px;
  height: 40px;
  opacity: 0;
}
.menu-wrap .hamburger {
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  width: 20px;
  height: 20px;
  padding: 0.75rem;
  background: -moz-linear-gradient(
    50% 96.29% 90deg,
    rgba(0, 0, 0, 0) 0%,
    rgba(41, 171, 226, 0.7) 100%
  );
  background: -webkit-linear-gradient(
    90deg,
    rgba(0, 0, 0, 0) 0%,
    rgba(41, 171, 226, 0.7) 100%
  );
  background: -webkit-gradient(
    linear,
    50% 96.29%,
    50% -5%,
    color-stop(0, rgba(0, 0, 0, 0)),
    color-stop(1, rgba(41, 171, 226, 0.7))
  );
  background: -o-linear-gradient(
    90deg,
    rgba(0, 0, 0, 0) 0%,
    rgba(41, 171, 226, 0.7) 100%
  );
  background: -ms-linear-gradient(
    90deg,
    rgba(0, 0, 0, 0) 0%,
    rgba(41, 171, 226, 0.7) 100%
  );
  -ms-filter: "progid:DXImageTransform.Microsoft.gradient(startColorstr='#000000', endColorstr='#29ABE2' ,GradientType=0)";
  background: linear-gradient(
    0deg,
    rgba(0, 0, 0, 0) 0%,
    rgba(41, 171, 226, 0.7) 100%
  );
  filter: progid:DXImageTransform.Microsoft.gradient(startColorstr='#29ABE2',endColorstr='#000000' , GradientType=0);
  opacity: 0.9;
  display: flex;
  align-items: center;
  justify-content: center;
}
.menu-wrap .hamburger > div {
  position: relative;
  width: 100%;
  height: 2px;
  background: #fff;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.4s ease;
}
.menu-wrap .hamburger > div::before,
.menu-wrap .hamburger > div::after {
  content: "";
  position: absolute;
  z-index: 1;
  top: -7px;
  width: 100%;
  height: 2px;
  background: #fff;
}
.menu-wrap .hamburger > div::after {
  top: 7px;
}
.menu-wrap .toggler:checked + .hamburger > div {
  transform: rotate(135deg);
}

.menu-wrap .toggler:checked + .hamburger > div::before,
.menu-wrap .toggler:checked + .hamburger > div::after {
  top: 0;
  transform: rotate(90deg);
}

.menu-wrap .toggler:checked ~ .menu {
  visibility: visible;
}
.menu-wrap .toggler:checked ~ .menu > div {
  transform: scale(1);
  transition-duration: 1s;
}
.menu-wrap .toggler:checked ~ .menu > div > div {
  opacity: 1;
  transition: opacity 0.4s ease;
}

.menu-wrap .menu {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 43px;
  visibility: hidden;
  overflow: hidden;
  display: flex;
  align-items: center;
  justify-content: center;
}

