  list-style: none;
  color: #fff;
  font-size: 0.9rem;
  padding: 1rem;
  display: inline;
}

.navbar ul {
  display: inline;
  list-style: none;
  text-align: right;
  margin-right: 150px;
  cursor: pointer;
  float: right;
}

.navbar ul {
  font-family: "Montserrat", sans-serif;

  color: #44aeeb;
  display: inline;
  text-decoration: none;
}

.navbar ul a {
  margin-right: 20px;
}
.logo-name {
  margin-top: 90px;
  margin-left: 200px;
}

.down_arrow {
  border: solid #fff;
  border-width: 0 1px 1px 0;
  display: inline-block;
  padding: 3px;
  transform: rotate(45deg);
  margin-left: 5px;
}

#cv_link {
  position: absolute;
  right: 100px;
  font-size: 0.7em;
  text-decoration: none;
  color: #fff;
}

#logo_white {
  width: 133px;
  display: none;
  cursor: pointer;
}

.main-text {
  display: block;
  margin-top: 250px;
  margin-left: auto;
  margin-right: auto;
}

